<template>
    <div class="container">
        <div class="data" v-if="posts">
            <div class="data__item" v-for="post in posts" :key="post.id">
                <h2>{{ post.title }}</h2>
                <p>{{ post.body }}</p>
            </div>
        </div>
        <div v-else aria-label="Orange and tan hamster running in a metal wheel" role="img" class="wheel-and-hamster">
            <div class="wheel"></div>
            <div class="hamster">
                <div class="hamster__body">
                    <div class="hamster__head">
                        <div class="hamster__ear"></div>
                        <div class="hamster__eye"></div>
                        <div class="hamster__nose"></div>
                    </div>
                    <div class="hamster__limb hamster__limb--fr"></div>
                    <div class="hamster__limb hamster__limb--fl"></div>
                    <div class="hamster__limb hamster__limb--br"></div>
                    <div class="hamster__limb hamster__limb--bl"></div>
                    <div class="hamster__tail"></div>
                </div>
            </div>
            <div class="spoke"></div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            posts: null
        }
    },
    mounted() {
        setTimeout(() => {
            this.getPosts()
        }, 1000);
    },
    methods: {
        async getPosts() {
            let response = await fetch('https://jsonplaceholder.typicode.com/posts')
            let answer = await response.json()
            this.posts = answer
        }
    },
}
</script>

